.home{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 15vh;
}

.home .left{
    display: flex;    
    padding-left: 2vw;
    flex-direction: column;
}

.home .right{
    display: flex;  
}

.home .right img{
    width: 50vw;
    height: 60vh;
    border-radius: 2vh;
}

.home .left h1{
    font-size: 6vh;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: 300;
}

.home .left p{
    font-size: 3vh;
}

.home .left .links{
    display: flex;
    flex-direction: column;
    gap: 3vh;
    font-size: 2.3vh;
    width: 40vw;
}

.home .left .links a{
    color: rgb(177, 4, 177);
}

.home .left .links a:hover{
    text-decoration: underline;
    padding-left: 2vw;
}

.home .left .collection{
    display: flex;
    align-items: center;
    gap: 2vw;
    margin-top: 5vh;
}

.home .left .collection button{
    background-color: white;
    color: black;
    border-radius: 15px;
    width: 16vw;
    height: 6vh;
    padding: 12px;
    font-size: 2vh;
    cursor: pointer;
}

.home .left .collection a{
    color: rgb(177, 4, 177);
    font-size: 2.4vh;
}

.home .left .collection img{
    width: 1.5vw;
    height: 3vh;
    background: none;
}

.home .left .collection .google p{
    font-size: 2vh;
    background: none;
    color: black;
}

.home .left .collection .google{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1vw;
}

.center{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 12vh;
}

.center .left img{
    margin-top: 20vh;
    width: 30vw;
}

.center .right h3{
    font-size: 4vh;
}

.center .right li{
    font-size: 2vh;
    padding: 2vh;
}

.center .right{
    padding: 5vh;
}

.center .right .buttons{
    display: flex;
    padding-left: 3vw;
    border-radius: 15px;
    gap: 1vw;
}

.center .right .buttons button{
    border-radius: 15px;
    width: 8vw;
    height: 5vh;
    padding: 12px;
    font-size: 1.5vh;
    cursor: pointer;
}

.center .right .buttons .learn button{
    background-color: white;
    color: black;
    font-weight: bold;
    border: 1px solid black;
}

.center .right .buttons .sign button{
    background-color: black;
    color: white;
    font-weight: bold;
    border: 1px solid black;
}

.center .right .buttons .learn button:hover{
    background-color: rgb(243, 151, 118);
    border-color: red;
    color: red;
}

.center .right .buttons .sign button:hover{
    background-color: rgb(245, 126, 146);
}

.cents{
    margin-top: 8vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10vh;
}

.carts{
    display: grid;
    grid-template-columns: auto auto;
    gap: 2vw;

}

@media only screen and (max-width: 1200px) {
    .home{
        display: flex;
        flex-direction: column-reverse;
        gap: 10vh;
    }

    .home .right img{
        width: 80vw;
    }

    .home .left .collection img{
        width: auto;
    }

    .center{
        display: flex;
        flex-direction: column;
        margin-top: 10vh;
    }

    .center .left img{
        margin-top: 0vh;
        width: 50vw;
    }

    .center .right .buttons .learn button{
        width: auto;
    }

    .center .right .buttons .sign button{
        width: auto;
    }

    .center .right{
        padding-top: 0vh;
    }

    .cents{
        margin-top: 0vh;
    }

    .home .left .collection button{
        width: auto;
    }
}

@media only screen and (max-width: 750px){
    .home{
        display: flex;
        flex-direction: column-reverse;
        gap: 10vh;
    }

    .center{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 12vh;
    }

    .center .left img{
        width: 60vw;
    }

    .carts{
        display: flex;
        flex-direction: column;
        gap: 4vh;
    }

    .center .right{
        padding-top: 0vh;
    }

    .cents{
        margin-top: 0vh;
    }

    .home .left h1{
        font-size: 4vh;
    }

    .home .left p{
        font-size: 2vh;
    }

    .center .right h3{
        font-size: 3vh;
    }
}

@media only screen and (max-width: 650px){
    .home .left .collection button{
        width: auto;
    }

    .home .left .collection img{
        width: 6vw;
        height: 3vh;
        background: none;
    }

    .home .right img{
        width: 80vw;
        height: 30vh;
    }

    .center{
        margin-top: auto;
    }

    .center .left img{
        margin-top: 7vh;
    }

    .center .right .buttons .learn button{
        width: auto;
    }

    .center .right .buttons .sign button{
        width: auto;
    }
}