.navbar{
    display: flex;
    flex-direction: row;    
    height: 10vh;
    align-items: center;
    font-size: 2vh;
}

.navbar .left{
    display: flex;
    flex: 50%;
    gap: 2vw;
    padding-left: 2vw;
}

.navbar .right{
    display: flex;
    justify-content: flex-end;
    flex: 50%;
    gap: 2vw;
    margin-right: 1vw;
    align-items: center;
}

a{
    text-decoration: none;
}

.navbar .right button{
    font-size: 2vh;
    border-radius: 5vh;
    padding: 12px;
    padding-left: 20px;
    padding-right: 20px;
    border: none;
    background-color: purple;
    color: white;
    cursor: pointer;
}

a:hover{
    text-decoration: underline;
}

.navbar .right button:hover{
    background-color: rgb(177, 4, 177);
}

.navbar .left h1{
    font-size: 3vh;
    cursor: pointer;
    color: white;
}

.navbar .left h1:hover{
    color: rgb(219, 219, 219);
}

.button{
    display: none;
}


@media only screen and (max-width: 750px) {
    a{
        font-size: 1.5vh;
    }

    .navbar .right{
        gap: 5vw;
    }

    .navbar #hidden{
        display: flex;
        position: absolute; 
        right: 0px;
        top: 10vh;
        height: 15vh;
        width: 100%;
        background-color: rgb(43, 5, 43);
        flex-direction: column;
        justify-content: center;
        z-index: 1;
        align-items: center;
    }

    .navbar .button{
        display: flex;
        margin-right: 3vw;
    } 


    .navbar .right{
        display: none;
    }

    .navbar .right button{
        width: auto;
        font-size: 1.2vh;
    }
}